@media(max-width: 992px) {
    .nav .my-nav {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 999;
        background: #181818;
        padding: 0px 10px;
        text-align: center;
    }
    .nav .my-nav a {
        color: rgb(129, 127, 127);
        font-size: 20px;
    }
    .nav .my-nav a i {
        padding: 20px;
    }
    .nav .break {
        height: 60px;
    }
}

@media(min-width: 992px) {
    .nav .my-nav {
        position: fixed;
        top: 0;
        left: 0;
        width: 60px;
        height: 100vh;
        z-index: 999;
        background: #181818;
        padding-top: 150px;
    }
    .nav .my-nav a {
        color: rgb(129, 127, 127);
        font-size: 20px;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .nav .my-nav a {
        display: block;
        width: 100%;
        text-align: center;
    }
}

.nav .my-nav .is-active {
    color: #ea4c89;
}